    font-family: var(--skydia);
    margin-bottom: 5rem;
  }

  .wrapper p {
    margin-bottom: 5rem;
  }
}

@media screen and (max-width: 485px) {
  .wrapper h1 {
    font-size: 6rem;
    font-family: 'Gruppo', cursive;
    font-family: var(--skydia);
  }
  
  .wrapper {
    margin-top: 10rem;
  }

  .wrapper p {
    font-size: 3rem;
  }

  .enter-wrapper button {
    font-size: 1.5rem;
  }
}
